
<template>
    <div>
        <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b">

            <el-menu-item index="1" @click="goto(1)">首页</el-menu-item>
            <el-menu-item index="2" @click="goto(2)">数据集下载</el-menu-item>
            <el-menu-item index="3" @click="goto(3)">论坛</el-menu-item>
            <el-menu-item index="4" @click="goto(4)">Top Solution</el-menu-item>
            <el-menu-item index="5" @click="goto(5)">AI 招聘</el-menu-item>
            <el-menu-item index="6" @click="goto(6)">关于我们</el-menu-item>

        </el-menu>
    </div>
</template>

<script>
export default {
  methods: {
    goto(v) {
      switch (v) {
        case 1:
          this.$router.push("/");
          break;
        case 2:
          this.$router.push("/datas");
          break;
        case 3:
          this.$router.push("/blogs");
          break;
        case 4:
          this.$router.push("/solution");
          break;
        case 5:
          this.$router.push("/recruit");
          break;
        case 6:
          this.$router.push("/about");
          break;
        default:
          this.$router.push("/errorx");
          break;
      }
    }
  },
  created() {
    this.activeIndex = this.cur;
  },
  data() {
    return {
      activeIndex: "1"
    };
  },
  props: {
    cur: {
      default: "1",
      type: String
    }
  }
};
</script>

<style>
</style>




