import request from '@/utils/request'
export function getDatasetTypes() {
  return request({
    url: '/dataset/getDatasetTypes',
    method: 'get',

  })
}

export function updatePaperDesc(p) {
  return request({
    url: '/dataset/updatePaperDesc',
    method: 'get',
    params: p
  })
}

export function addDatasetType(p) {
  return request({
    url: '/dataset/addDatasetType',
    method: 'get',
    params: p

  })
}


export function getDatasets(p) {
  return request({
    url: '/dataset/getDatasets',
    method: 'get',
    params: p

  })
}




export function dataDetail(p) {
  return request({
    url: '/dataset/dataDetail',
    params: p
  })
}

export function addDataset(p) {
  return request({
    url: '/dataset/addDataset',
    params: p
  })
}


export function searchDataset(p) {
  return request({
    url: '/dataset/searchDataset',
    params: p
  })
}

export function findDatasetById(p) {
  return request({
    url: '/dataset/findDatasetById',
    params: p
  })
}

export function editDataset(p) {
  return request({
    url: '/dataset/editDataset',
    params: p
  })
}

export function delDataset(p) {
  return request({
    url: '/dataset/delDataset',
    params: p
  })
}



