import request from '@/utils/request'
export function getSolutions(para) {
  return request({
    url: '/solution/getSolutions',
    method: 'get',
    params: para
  })
}

export function getSolution(para) {
  return request({
    url: '/solution/getSolution',
    method: 'get',
    params: para
  })
}

export function addSolution(para) {
  return request({
    url: '/solution/addSolution',
    method: 'get',
    params: para
  })
}

export function delSolution(para) {
  return request({
    url: '/solution/delSolution',
    method: 'get',
    params: para
  })
}



