<template>
  <!-- qiao-new -->
  <div>
    <header class="header">
      <div class="header__top">
        <div class="container-fluid">
          <div class="row">
            <div class="col-lg-6 col-md-8 col-sm-6 col-xs-12">
              <p>xixiaokou-Beijing 457361577@qq.com</p>
            </div>
            <div class="col-lg-6 col-md-4 col-sm-6 col-xs-12">
              <div class="header__actions">
                <router-link v-if="!info.name" to="/login">登陆/注册</router-link>
                <a v-if="info.name">欢迎回来:{{dispalyUsername}}</a>
                <a v-if="info.name" @click="logout()">退出</a>

                <div class="btn-group ps-dropdown">
                  <router-link class="dropdown-toggle flat40" to="/">首页</router-link>
                </div>

              </div>
            </div>
          </div>
        </div>
      </div>
    </header>
  </div>
</template>


<script>
import { getInfo } from "@/api/login";
import { getToken } from "@/utils/auth";
export default {
  name: "flatGreyHead",
  created() {
    this.reloadLoginStatus();
  },

  computed: {
    dispalyUsername() {
      return this.$store.getters.name;
    }
  },
  methods: {
    // 退出登录
    logout() {
      // 发出清空token消息
      this.$store.dispatch("FedLogOut");

      // 重定向
      // this.$router.push({path: '/login'});
      window.location.href = "#/login";
    },
    reloadLoginStatus() {
      // 如果token有值，尝试读取$store
      if (getToken()) {
        this.info.roles = this.$store.getters.roles;
        this.info.name = this.$store.getters.name;
        this.info.avatar = this.$store.getters.avatar;
        this.info.token = this.$store.getters.token;

        // 如果有任意一个为空 则认为没有登陆
        return (
          this.info.roles &&
          this.info.name &&
          this.info.avatar &&
          this.info.token
        );
      } else {
        return false;
      }
    }
  },
  data() {
    return {
      info: {}
    };
  }
};
</script>



<style scoped>
@import url(./../../assets/css/style2.css);
@import url(./../../assets/plugins/bootstrap/dist/css/bootstrap.min.css);

.flat40 {
  height: 40px;
}
</style>



