import request from '@/utils/request'
export function addBlog(params) {
  return request({
    url: '/blog/addBlog',
    method: 'post',
    params: params
  })
}

export function getBlogByTarget(p) {
  return request({
    url: '/blog/getBlogByTarget',
    method: 'get',
    params:p
  })
}


export function getBlog(para) {
  return request({
    url: '/blog/getBlog',
    method: 'get',
    params: para
  })
}

export function getBlogs(para) {
  return request({
    url: '/blog/getBlogs',
    method: 'get',
    params: para
  })
}

export function blogComments(para) {
  return request({
    url: '/blog/blogComments',
    method: 'get',
    params: para
  })
}


export function addBlogComment(para) {
  return request({
    url: '/blog/addBlogComment',
    method: 'get',
    params: para
  })
}


export function getBlogTypes() {
  return request({
    url: '/blog/getBlogTypes',
    method: 'get',

  })
}


export function editBlog(p) {
  return request({
    url: '/blog/updateBlog',
    method: 'post',
    params: p
  })
}

export function delBlog(p) {
  return request({
    url: '/blog/delBlog',
    method: 'post',
    params: p
  })
}






